.sidebar{
    flex: 3;
    height: calc(100vh - 50px);
    overflow-y: scroll;
    position: sticky;
    top: 50px;
}
::-webkit-scrollbar{
    width: 5px;
}
::-webkit-scrollbar-track{
    background-color: #f1f1f1;
}
::-webkit-scrollbar-thumb{
    background-color: rgb(171, 168, 168);
}

.sidebarWrapper{
    padding: 20px;
}
.sidebarList{
    padding: 0;
    margin: 0;
    list-style:none ;
}
.sidebarListItem{
    display: flex;
    align-items: center;
    margin-bottom: 20px;
}
.sidebarIcon{
    margin-right: 15px;
}

.sidebarButton{
    width: 150px;
    border: none;
    padding: 10px;
    border-radius: 5px;
    font-weight: 500;
}
.sidebarHr{
    margin: 20px 0px;
}
.sidebarFriendList{
    padding: 0;
    margin: 0;
    list-style: none;
} 




/* Media Queries for Mobile */
@media (max-width: 768px) {
    .sidebar {
        flex: 1;
        height: auto;
        margin-bottom: 20px;
    }
    .sidebarWrapper{
        display: none;
    }
}
